export default {
	data() {
		return {
			loadStatus: 'more',
			searchVal: '',
			canSearch: true,
			timer: null,
			userList:[],
			pageNum:1,
			total:"",
			pageSize:15
		}
	},
	created() {
		const that = this;
		that.appuserSearch(1);
	},
	
	methods: {
		appuserSearch(startPage){
			const that = this;
			let param = {
				username: that.searchVal,
				startPage:startPage,
				pageSize:that.pageSize
			}
			that.loadStatus="loading";
			that.$api.appuserSearch(param).then(res => {
				if (res.success) {
					let result = res.datas.rows;
					that.total=res.datas.total;
					for(var i in result){
						result[i].headImgUrl = result[i].headImgUrl?result[i].headImgUrl:'../../../static/images/defaultUserPic.png';
						result[i].nickname=result[i].nickname?result[i].nickname:result[i].username;
						that.userList.push(result[i]);
					}
					let total = res.datas.total;
					let totalPageNum =Math.ceil(total/that.pageSize);
					if(parseInt(totalPageNum)>parseInt(that.pageNum)){
						that.pageNum++;
						that.loadStatus="more";
					}
					else{
						that.loadStatus="noMore";
					}
				} else {
					uni.showToast({
						title: res.message,
						icon: "none"
					})
				}
			})
		},
		
		searchCancel() {
			this.searchVal = "";
			const that = this;
			that.clearList();
			that.appuserSearch(1);
			uni.hideKeyboard();
		},
		constantlySearch(e) {
			const that = this;
			clearTimeout(that.timer);
			that.timer = setTimeout(function() {
				// that.appuserSearch();
			}, 800);
		},
		confirmSearch(){
			const that = this;
			that.clearList();
			that.appuserSearch(1);
		},
		clearList(){
			const that = this;
			that.pageNum=1;
			that.total="";
			that.userList=[];
		},
		selectItem(id, name) {
			const that = this;
			var pages = getCurrentPages(),
				prevPage = null;
			if (pages.length > 1) {
				prevPage = pages[pages.length - 2];
			}
			if (prevPage) {





				prevPage.$vm.customerId = id;
				prevPage.$vm.customerName = name

			}
			setTimeout(() => {
				that.$Router.back(1);
			}, 100)
		}
	},
	onReachBottom() {
		const that = this;
		if(that.loadStatus=="noMore"){
			return;
		}
		else if(that.pageNum!=1){
			that.appuserSearch(that.pageNum);
		}
	},
}
